.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body
{
  font-family:sans-serif;
  font-weight: 400;
  line-height: 1.666;
  /*background: #c0d2d4;*/
  background-color: #bcc0c0;
  color: #555;
}
h1
{
  font-weight: 900;
  font-size: 500%;
  text-align: center;
  margin-top: 4rem;
  color: #fff;
  line-height: 1.2;
}
h1 span
{
  padding: 5px 15px;
  background: #2ec2b0;
}
.dictionary
{
  display: flex;
  flex-wrap: wrap;
  justify-content:space-between;
  margin: 4rem auto;
  max-width:90%;
}
.dictionary >.term
{
  text-align: center;
  max-width: 20rem;
  margin-bottom: 3rem;
  padding: 2rem;
  border-radius: 25px;
  box-shadow: 0 2px 0 rgba(0,0,0,0.05),0 5px 20px rgba(0,0,0,0.1);
  background-color: rgb(243, 237, 237);
  transition: all 100ms ease-in-out;
}
.dictionary >.term >dt
{
  margin-bottom:1rem;
}
dt
{
  color: #2ec2b0;
  line-height: 1;
}
dt span
{
  font-size: 200%;
  font-weight: 900;
}
.emoji
{
  margin: 0 auto 20px;
  display: block;
  font-size: 400%;
}
dd
{
  font-size: 92.5%;
  margin-left: 0;
}
dd dt
{
  margin-top: 1rem;
  margin-bottom: 0.25rem;
  font-weight: 600;
  font-size: inherit;
}
dd dl
{
  font-size: 90%;
  margin:1rem auto 0 auto;
  letter-spacing: 0.33px;
  max-width: 15rem;
}
dd dl  .term
{
  margin-bottom: 1rem;
}